.stweb__pt-banner-hero {
  height: 400px;
  position: relative;
  padding: 40px 16px;

  @media (min-width: 768px) {
      padding: 60px 15px;
  }

  &:before {
      @media (min-width: 768px) {
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          z-index: 1;
          background: linear-gradient(94.15deg, rgba(11, 39, 57, 0.15) 10.75%, rgba(1, 157, 244, 0.15) 63.51%, rgba(3, 134, 208, 0.15) 87.66%, rgba(11, 39, 57, 0.15) 101.44%);
      }
  }

  .picture {
      &--bg {
          img {
              position: absolute;
              width: 100%;
              height: 100%;
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
              object-fit: cover;
              object-position: center;
          }
      }
  }

  .content {
      position: relative;
      z-index: 2;

      .title {
          font-weight: 300;
          font-size: 28px;
          line-height: 32px;
          text-align: center;
          color: #FFFFFF;
          margin-bottom: 8px;

          @media (min-width: 768px) {
              font-size: 48px;
              line-height: 48px;
          }
      }

      .text {
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          text-align: center;
          letter-spacing: -0.0036em;
          color: #FFFFFF;
          margin-bottom: 24px;

          @media (min-width: 768px) {
              font-size: 18px;
              line-height: 30px;
              margin-bottom: 36px;
          }
      }

      .promotion {
          text-align: center;
      }
  }
}

.stweb__pt-tabs-Planes {
    position: relative;
    z-index: 1;
    padding: 0 0 20px;

    @media (min-width: 768px) {
        padding: 0 0 40px;
    }

    .tab-panel .tab-content .tab-pane {
        overflow: inherit;
        .swiper-wrapper{
            @media (min-width: 992px) {
                justify-content: center;
            }
        }
    }

    .coverage {
        position: relative;
        margin-top: -125px;

        @media (min-width: 768px) {
            margin-top: -60px;
        }

        .content {
            width: 100%;
            max-width: 328px;
            margin: 0 auto;
            padding: 15px;
            background: #FFFFFF;
            box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
            border-radius: 8px;

            @media (min-width: 768px) {
                max-width: 896px;
                padding: 15px 24px;
            }

            .row {
                align-items: center;
                justify-content: center;

                @media (min-width: 768px) {
                    justify-content: space-between;
                }
            }

            .picture {
                text-align: center;
                display: block;
                margin-bottom: 16px;

                @media (min-width: 768px) {
                    text-align: left;
                    margin-bottom: 0;
                }
            }

            .text {
                font-weight: 300;
                font-size: 18px;
                line-height: 24px;
                color: #0B2739;
                text-align: left;
                margin: 0 0 16px;

                @media (min-width: 768px) {
                    font-size: 28px;
                    line-height: 32px;
                    margin: 0;
                }

                .info {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 15px;
                    color: #50535A;
                    display: block;
                    margin-top: 8px;
                }
            }

            .stweb-btn--cian {
                width: 100%;
                max-width: 208px;
                font-size: 16px;
                line-height: 24px;
                height: 56px;

                @media (min-width: 929px) {
                    padding-left: 32px;
                    padding-right: 32px;
                }

                @media (max-width: 930px) and (min-width: 767px) {
                    padding-left: 7%;
                    padding-right: 7%;

                }

                @media(max-width: 768px) {
                    display: block;
                    margin: 0 auto;
                    height: 40px;
                }
            }
        }
    }

    .tab-panel {
        margin-top: 0px;
    }

    .tab-pane-footer {
        padding: 40px 0 0;

        .container {
            padding: 0;
        }

        .title {
            font-weight: 300;
            font-size: 28px;
            line-height: 32px;
            color: #50535A;
            text-align: center;
            padding: 0 15px;
            margin-bottom: 10px;

            @media (min-width: 768px) {
                margin-bottom: 20px;
            }
        }

        .plans-include {
            width: 100%;
            margin: 0 auto;
            display: flex;
            padding: 20px;

            &.plans-include-col-2 {
                max-width: 580px;
            }

            &.plans-include-col-3 {
                max-width: 1110px;
            }

            &__item {
                background: #FFFFFF;
                box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
                border-radius: 16px;
                display: flex;
                align-items: center;
                width: 255px;
                height: 96px;
                padding: 15px;

                .picture {
                    margin-right: 8px;
                }

                .content {
                    .title {
                        font-weight: 700;
                        font-size: 14px;
                        line-height: 20px;
                        letter-spacing: 0.12px;
                        color: #0B2739;
                        margin-bottom: 4px;
                        text-align: left;
                        padding: 0;
                    }

                    .text {
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 20px;
                        letter-spacing: 0.12px;
                        color: #50535A;
                    }
                }
            }
        }
    }

    .pt-plan__carousel {
        position: relative;

        .swiper {
            margin: 0 auto;
        }

        .swiper-wrapper {
            @media (max-width: 575.98px) {
                transform: none!important;
                flex-direction: column;
            }
        }

        .swiper-slide {
            padding: 15px 10px;

            @media (max-width: 575.98px) {
                padding: 15px 10px;
            }
        }

        .swiper-button-prev {
            right: -30px;
            left: auto;
            top: var(--swiper-navigation-top-offset, 47%);
        }

        .swiper-button-next {
            right: -30px;
            top: var(--swiper-navigation-top-offset, 53%);
        }

        .swiper-button-prev,
        .swiper-button-next {
            font-size: 10px;
            width: 28px;
            height: 28px;
            margin-top: 0px;
            border-radius: 50%;
            background-color: #019DF4;
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

            @media (min-width: 768px) {
                font-size: 12px;
            }

            @media (max-width: 575.98px) {
                display: none;
            }

            &::after {
                content: inherit;
            }

            &.swiper-button-disabled {
                background-color: #fff;
                box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.25);

                >.icon {
                    path {
                        stroke: #019DF4;
                    }
                }
            }

            >.icon {

                path {
                    stroke: #fff;
                }
            }
        }
    }

    .plan-box {
        position: relative;
        background: #FFFFFF;
        box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
        border-radius: 8px;
        padding: 24px 0px;

        @media (max-width: 575.98px) {
            overflow: hidden;
            padding: 24px 0px 0;
        }

        &.active {
            .box__footer .link--more .svg--inline {
                transform: rotate(180deg);
            }
        }

        &.plan-box-great {
            padding: 0px 0px 24px;

            @media (max-width: 575.98px) {
                padding: 0px;
            }

            .box__info {
                padding-top: 24px;
            }
        }

        &.plan-box-only {
            max-width: 330px;
            margin: 0 auto;

            @media (min-width: 768px) {
                max-width: 202px;
            }
        }

        .box__outstanding {
            display: flex;
            justify-content: center;
            width: 100%;
            height: 24px;
            align-items: center;
            background: #A13EA1;
            color: #fff;
            //border-radius: 8px 8px 0px 0px;
            font-weight: 700;
            font-size: 10px;
            margin: 0 auto;
            text-align: center;
            line-height: 1;
            text-transform: uppercase;

            @media (min-width: 576px) {
                position: absolute;
                border-radius: 20px;
                width: 97px;
                height: 18px;
                left: calc(50% - 97px/2 + 0.5px);
                top: -9px;
            }
        }

        .box__info {
            padding: 0 12px 16px;

            @media (max-width: 575.98px) {
                padding: 0 16px 16px;
            }

            .info-megas {
                @media (max-width: 575.98px) {
                    display: flex;
                    flex-wrap: wrap;
                }

                .megas__col-full {
                    @media (max-width: 575.98px) {
                        position: relative;
                        width: 100%;
                        flex: 0 0 100%;
                        max-width: 100%;
                    }
                }

                .megas__col-1 {
                    @media (max-width: 575.98px) {
                        position: relative;
                        width: 100%;
                        flex: 0 0 50%;
                        max-width: 50%;
                    }

                    &::after {
                        @media (max-width: 575.98px) {
                            content: "";
                            position: absolute;
                            top: 10px;
                            bottom: 0;
                            right: 0;
                            display: block;
                            width: 1px;
                            height: 36px;
                            background-color: #D3D4D3;
                        }
                    }
                }

                .megas__col-2 {
                    @media (max-width: 575.98px) {
                        position: relative;
                        width: 100%;
                        flex: 0 0 50%;
                        max-width: 50%;
                    }
                }
            }

            .info-text-1 {
                font-size: 18px;
                line-height: 1.2;
                color: #0B2739;
                text-align: center;
                margin-bottom: 5px;

                @media (max-width: 575.98px) {
                    font-size: 22px;
                }
            }

            .info-text-2 {
                font-size: 12px;
                line-height: 1.2;
                color: #86888C;
                text-align: center;
                margin-bottom: 8px;

                @media (max-width: 575.98px) {
                    font-size: 14px;
                }
            }

            .info-text-3 {
                font-size: 38px;
                line-height: 1.2;
                color: #019DF4;
                text-align: center;
                margin-bottom: 4px;

                @media (max-width: 575.98px) {
                    font-size: 32px;
                }
            }

            .info-text-4 {
                font-size: 12px;
                line-height: 1.2;
                color: #0B2739;
                text-align: center;
                margin-bottom: 8px;
            }

            .info-text-5 {
                font-size: 32px;
                font-weight: 700;
                line-height: 1.2;
                color: #0B2739;
                text-align: center;
                margin-bottom: 0px;

                @media (max-width: 575.98px) {
                    margin-bottom: 4px;
                }

                .text-symbol {
                    font-size: 20px;
                    font-weight: 400;

                    @media (max-width: 575.98px) {
                        font-size: 16px;
                    }
                }

                .text-sub {
                    font-size: 12px;

                    @media (max-width: 575.98px) {
                        font-size: 10px;
                    }
                }
            }

            .info-text-6 {
                font-size: 12px;
                line-height: 1.2;
                color: #50535A;
                text-align: center;
                margin-bottom: 16px;
            }

            .info-text-7 {
                font-size: 14px;
                line-height: 1.2;
                color: #019DF4;
                text-align: center;
                margin-bottom: 4px;

                @media (max-width: 575.98px) {
                    font-size: 16px;
                }
            }

            .info-text-8 {
                font-size: 12px;
                line-height: 1.2;
                color: #0B2739;
                text-align: center;
                margin-bottom: 16px;

                .text-symbol {
                    color: #019DF4;
                }
            }

            .button--buy {
                display: block;
                width: 100%;
                font-size: 16px;
                padding-left: 8px;
                padding-right: 8px;
            }

            .button--call {
                display: block;
                width: 100%;
                font-size: 16px;
                margin-top: 8px;
                padding-left: 8px;
                padding-right: 8px;
            }

            .info-promo-1 {
                display: block;
                background: #F4F4F4;
                border-radius: 8px;
                padding: 6px 4px;
                margin-top: 16px;

                @media (max-width: 575.98px) {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    padding: 13px 4px;
                }

                .promo-1__col-1 {
                    @media (max-width: 575.98px) {
                        position: relative;
                        width: 100%;
                        flex: 0 0 50%;
                        max-width: 50%;
                    }
                }

                .promo-1__col-2 {
                    @media (max-width: 575.98px) {
                        position: relative;
                        width: 100%;
                        flex: 0 0 50%;
                        max-width: 50%;
                    }
                }

                .promo-text-1 {
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 1.2;
                    text-align: center;
                    color: #0B2739;
                    margin-bottom: 5px;

                    @media (max-width: 575.98px) {
                        margin-bottom: 0;
                    }
                }

                .promo-text-2 {
                    font-size: 10px;
                    font-weight: 700;
                    line-height: 1.2;
                    text-align: center;
                    color: #50535A;
                    margin-bottom: 0px;

                    .text-color {
                        color: #019DF4;
                    }
                }
            }

            .info-promo-2 {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                background: #F4F4F4;
                border-radius: 8px;
                padding: 8px 4px;
                margin-top: 16px;

                .promo-col-1 {
                    position: relative;
                    width: 100%;
                    flex: 0 0 50%;
                    max-width: 50%;
                }

                .promo-col-2 {
                    position: relative;
                    width: 100%;
                    flex: 0 0 50%;
                    max-width: 50%;
                }

                .promo-image-1 {
                    display: block;
                    width: auto;
                    height: auto;
                    max-width: 100%;
                    margin: 0 auto;
                }

                .promo-text-1 {
                    font-size: 12px;
                    font-weight: 700;
                    line-height: 1.2;
                    text-align: center;
                    color: #0B2739;
                    margin-bottom: 2px;

                    .text-1__sub {
                        display: block;
                        font-size: 10px;
                        font-weight: 400;
                        line-height: 1.2;
                        text-align: center;
                        color: #50535A;
                        margin-bottom: 0px;

                        @media (max-width: 575.98px) {
                            display: inline-block;
                            font-size: 8px;
                        }
                    }
                }

                .promo-text-2 {
                    font-size: 10px;
                    font-weight: 400;
                    line-height: 1.2;
                    text-align: center;
                    color: #50535A;
                    margin-bottom: 0px;

                    @media (max-width: 575.98px) {
                        font-size: 8px;
                    }

                    .text-color {
                        color: #019DF4;
                    }
                }

                .promo-sticker {
                    display: flex;
                    align-items: center;
                    max-width: 65px;
                    margin: 0 auto;
                    .sticker__svg {
                        font-size: 20px;
                    }
                    .sticker__text {
                        flex: 1;
                        display: block;
                        font-size: 10px;
                        font-weight: 400;
                        line-height: 1.15;
                        color: #50535A;
                        margin-left: 5px;
                    }
                }
            }
        }

        .box__description {
            padding: 0 12px;
            border-top: 1px solid #F6F6F6;

            @media (max-width: 575.98px) {
                padding: 0 16px;
                display: none;
            }

            .item__benefit {
                display: flex;
                align-items: flex-start;
                margin-top: 16px;

                .benefit__icon {
                    display: block;
                    width: 15px;
                    margin-right: 10px;

                    img {
                        width: auto;
                        height: auto;
                        vertical-align: top;
                    }
                }

                .benefit__info {
                    flex: 1;

                    .info-text-1 {
                        font-size: 12px;
                        line-height: 1.2;
                        color: #313235;
                        margin-bottom: 4px;

                        @media (max-width: 575.98px) {
                            font-size: 14px;
                        }
                    }

                    .info-text-2 {
                        font-size: 12px;
                        line-height: 1.2;
                        color: #50535A;
                        margin-bottom: 4px;

                        @media (max-width: 575.98px) {
                            font-size: 14px;
                        }
                    }

                    .info-text-3 {
                        font-size: 12px;
                        line-height: 1.2;
                        color: #86888C;
                        margin-bottom: 0;

                        @media (max-width: 575.98px) {
                            font-size: 14px;
                        }
                    }
                }
            }
        }

        .box__footer {
            display: none;
            position: relative;
            padding: 0;

            @media (max-width: 575.98px) {
                display: block;
            }

            .link--more {
                display: block;
                width: 100%;
                text-align: center;
                font-size: 16px;
                font-weight: 400;
                line-height: 1.2;
                background-color: transparent;
                border: 0;
                padding: 16px 0 24px;
                -ms-user-select: none;
                user-select: none;
                cursor: pointer;
                color: #019DF4;
                outline: 0;
                transition: .4s;

                .svg--inline {
                    height: 13px;
                    width: 13px;
                    margin-left: 5px;
                    filter: invert(51%) sepia(81%) saturate(3888%) hue-rotate(175deg) brightness(101%) contrast(99%);
                }
            }
        }
    }
}

.stweb__pt-why {
  position: relative;
  padding: 24px 0;
  background-color: #F6F6F6;

  @media (min-width: 992px) {
      padding: 60px 0;
  }

  .pt-why {
      &__title {
          font-size: 28px;
          font-weight: 300;
          line-height: 1.2;
          text-align: center;
          color: #313235;
          margin-bottom: 32px;

          @media (min-width: 992px) {
              font-size: 38px;
              margin-bottom: 40px;
          }
      }

      &__cards {
          margin-left: -8px;
          margin-right: -8px;

          [class^='col-'] {
              @media (max-width: 991.98px) {
                  padding-left: 8px;
                  padding-right: 8px;
              }
          }
      }

      &__box {
          position: relative;
          display: block;
          width: 100%;
          height: 196px;
          background: #fff;
          padding: 16px 16px;
          margin: 8px 0;
          box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
          border-radius: 8px;

          @media (min-width: 768px) {
              padding: 16px 10px;
          }

          @media (min-width: 992px) {
              display: flex;
              align-items: center;
              height: 108px;
              padding: 24px 16px;
              margin: 15px 0;
          }

          .box__header {
              margin-bottom: 16px;

              @media (min-width: 992px) {
                  margin-right: 16px;
              }

              @media (max-width: 991.98px) {
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  height: 76px;
              }

              picture,
              img {
                  display: block;
              }

              img {
                  width: auto;
                  margin: 0 auto;
                  max-width: 100%;
              }
          }

          .box__body {
              @media (min-width: 992px) {
                  flex: 1;
              }

              .box__title {
                  font-size: 14px;
                  text-align: center;
                  line-height: 1.2;
                  font-weight: 700;
                  color: #0B2739;
                  letter-spacing: 0.12px;
                  margin-bottom: 4px;

                  @media (min-width: 992px) {
                      font-size: 14px;
                      text-align: left;
                  }

                  @media (min-width: 1200px) {
                      font-size: 16px;
                  }

                  a {
                      color: #0B2739;
                      text-decoration: none;

                      &::after {
                          content: "";
                          position: absolute;
                          top: 0;
                          left: 0;
                          width: 100%;
                          height: 100%;
                      }
                  }
              }

              .box__description {
                  font-size: 14px;
                  line-height: 1.15;
                  color: #86888C;
                  margin-bottom: 0;
                  text-align: center;

                  @media (min-width: 992px) {
                      font-size: 14px;
                      text-align: left;
                  }

                  @media (min-width: 1200px) {
                      font-size: 16px;
                  }
              }
          }
      }

      &__banner {
          position: relative;
          display: block;

          @media (max-width: 991.98px) {
              margin-bottom: 20px;
          }

          img {
              display: block;
              margin: 0 auto;
              border-radius: 8px;
          }
      }
  }
}

