      &__description {
          font-size: 14px;
          font-weight: 400;
          line-height: 1.2;

          @media (min-width: 992px) {
              font-size: 18px;
              max-width: 520px;
          }

          >* {
              font-size: 14px;

              @media (min-width: 992px) {
                  font-size: 18px;
              }

              &:last-child {
                  margin-bottom: 0;
              }
          }
      }

      &__banner {
          position: relative;
          display: block;

          @media (max-width: 991.98px) {
              margin-top: 24px;
              margin-bottom: 24px;
          }

          img {
              display: block;
              margin: 0 auto;
              border-radius: 8px;
          }
      }

      &__benefit {
          position: relative;
          background: #fff;
          padding: 20px 0px;
          margin-top: 24px;
          margin-left: 0;
          margin-right: 0;
          box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
          border-radius: 8px;

          @media (min-width: 992px) {
              margin-top: 48px;
              padding: 25px 20px;
          }

          .benefit__box {
              display: block;
              width: 100%;
              max-width: 227px;
              margin: 0 auto;
              padding: 0 5px;

              @media (max-width: 991.98px) {
                  margin: 8px auto;
                  padding: 0 5px;
              }

              .box__icon {
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  height: 35px;
                  margin-bottom: 20px;

                  @media (min-width: 992px) {
                      height: 50px;
                  }

                  >.icon {
                      font-size: 34px;
                      color: #019DF4;

                      @media (min-width: 992px) {
                          font-size: 50px;
                      }
                  }
              }

              .box__description {
                  font-size: 12px;
                  font-weight: 400;
                  text-align: center;

                  @media (min-width: 992px) {
                      font-size: 14px;
                  }

                  >* {
                      font-size: 12px;

                      @media (min-width: 992px) {
                          font-size: 14px;
                      }

                      &:last-child {
                          margin-bottom: 0;
                      }
                  }
              }
          }
      }

      &__banner-repetidor {
          position: relative;
          background: #fff;
          padding: 20px 0px;
          margin-top: 24px;
          margin-left: 0;
          margin-right: 0;
          box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
          border-radius: 8px;

          [class^='col'] {
              z-index: 1;
          }

          @media (min-width: 992px) {
              margin-top: 48px;
              padding: 24px 25px;
          }

          .banner__qr {
              @media (max-width: 767.98px) {
                  display: none;
              }

              .qr__picture {
                  >img {
                      display: block;
                      width: auto;
                      height: auto;
                      max-width: 100%;
                      border: 1px solid #dfdfdf;
                      border-radius: 8px;
                      overflow: hidden;
                      box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
                  }
              }
          }

          .banner__background {
              display: none;

              @media (min-width: 768px) {
                  display: block;
              }

              >img {
                  position: absolute;
                  width: 100%;
                  height: auto;
                  left: auto;
                  right: 0;
                  /* top: 0; */
                  bottom: 0;
                  object-fit: none;
                  object-position: bottom right;
                  z-index: 0;

                  @media (min-width: 1024px) {
                      right: 70px;
                  }
              }
          }

          .banner__box {
              display: flex;
              align-items: flex-start;
              max-width: 350px;

              .box__icon {
                  margin-top: 4px;
                  margin-right: 16px;
              }

              .box__info {
                  flex: 1;
              }
          }

          .banner__title {
              font-size: 18px;
              font-weight: 300;
              line-height: 1.3;
              color: #313235;
              margin-bottom: 8px;

              @media (min-width: 992px) {
                  font-size: 24px;
              }

              .title__bold-400 {
                  @media (min-width: 992px) {
                      font-weight: 400;
                  }
              }
          }

          .banner__description {
              font-size: 12px;
              font-weight: 400;
              line-height: 1.3;
              color: #50535A;
              margin-bottom: 0px;

              @media (min-width: 992px) {
                  font-size: 14px;
              }
          }

          .banner__apps {
              display: flex;
              align-items: center;
              margin-top: 8px;

              .app__google,
              .app__ios {
                  display: inline-block;
              }

              .app__google {
                  margin-left: 8px;
              }
          }
      }
  }
}

.stweb {
  &__pt-clients {
      padding: 24px 0 40px;

      @media (min-width: 600px) {
          padding: 60px 0 80px;
      }

      .title {
          font-weight: 300;
          font-size: 38px;
          line-height: 46px;
          text-align: center;
          color: #313235;

          @media (min-width: 600px) {
              font-size: 28px;
              line-height: 32px;
          }
      }

      .client {
          display: flex;
          align-items: center;
          padding-top: 12px;
          padding-bottom: 12px;

          .data {
              width: 100%;
              padding: 13px 9px;
              position: relative;
              max-width: 105px;
              max-height: 105px;
              border-radius: 8px;

              @media (min-width: 600px) {
                  max-width: 180px;
                  max-height: 176px;
                  padding: 36px 16px;
              }

              .title {
                  font-weight: 700;
                  font-size: 12px;
                  line-height: 16px;
                  text-align: left;
                  color: #313235;
                  margin-bottom: 8px;
                  width: 100%;
                  max-width: 135px;

                  @media (min-width: 600px) {
                      font-size: 18px;
                      line-height: 24px;
                  }
              }

              .plan {
                  font-weight: 400;
                  font-size: 10px;
                  line-height: 14px;
                  color: #50535A;
                  width: 100%;
                  max-width: 135px;
                  display: block;

                  @media (min-width: 600px) {
                      font-size: 12px;
                      line-height: 16px;
                  }
              }

              .photo {
                  position: absolute;
                  right: -25px;
                  top: 0;

                  @media (min-width: 600px) {
                      right: -135px;
                  }

                  .picture {
                      width: 66px;

                      @media (min-width: 600px) {
                          width: auto;
                      }
                  }
              }
          }

          &--bg {
              position: absolute;
              z-index: -1;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              object-fit: cover;
          }

          .testimony {
              padding-left: 23px;

              @media (min-width: 600px) {
                  padding-left: 128px;
              }

              .paragraph {
                  .text {
                      font-size: 12px;
                      line-height: 18px;
                      color: #50535A;
                      margin-bottom: 0;

                      &:first-of-type {
                          margin-bottom: 15px;
                      }

                      @media (min-width: 600px) {
                          font-size: 14px;
                          line-height: 18px;

                          &:first-of-type {
                              margin-bottom: 20px;
                          }
                      }
                  }
              }
          }
      }
  }
}

.stweb__pt-browse {
  position: relative;
  padding: 34px 0 92px;
  background-color: #F6F6F6;

  @media (min-width: 992px) {
      padding: 70px 0 120px;
  }

  .pt-browse {
      &__title {
          font-size: 18px;
          font-weight: 300;
          line-height: 1.2;
          text-align: center;
          color: #313235;
          margin-bottom: 10px;

          @media (min-width: 992px) {
              font-size: 20px;
              margin-bottom: 10px;
          }
      }

      &__subtitle {
          font-size: 28px;
          font-weight: 300;
          line-height: 1.2;
          text-align: center;
          color: #313235;
          margin-bottom: 32px;

          @media (min-width: 992px) {
              font-size: 38px;
              margin-bottom: 40px;
          }
      }

      &__carousel {
          position: relative;

          .swiper,
          .swiper-container {
              max-width: 1014px;
              margin: 0 auto;
          }

          .swiper-slide {
              padding: 0 15px;
          }

          .swiper-button-prev {
              left: 0;
          }

          .swiper-button-next {
              right: 0;
          }

          .swiper-button-prev,
          .swiper-button-next {
              font-size: 10px;
              width: 28px;
              height: 28px;
              margin-top: 0px;
              border-radius: 50%;
              background-color: #019DF4;
              box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

              @media (min-width: 768px) {
                  font-size: 12px;
              }

              &::after {
                  content: inherit;
              }

              &.swiper-button-disabled {
                  background-color: #fff;

                  >.icon {
                      path {
                          stroke: #019DF4;
                      }
                  }
              }

              >.icon {

                  path {
                      stroke: #fff;
                  }
              }
          }

          .swiper-pagination {
              width: 100%;
              font-size: 14px;
              color: #50535A;
              bottom: -40px;
              left: 0;
              right: 0;

              @media (min-width: 768px) {
                  max-width: 100%;
                  font-size: 16px;
              }

              .swiper-pagination-bullet {
                  width: 10px;
                  height: 10px;
                  margin: 0 5px;
              }
          }

          .swiper-pagination-bullet-active {
              background-color: #019DF4;
          }
      }

      &__box {
          position: relative;
          display: block;
          width: 100%;
          height: 142px;
          background: #fff;
          padding: 15px 16px;
          margin: 20px 0;
          box-shadow: 0px 2px 4px rgba(11, 39, 57, 0.05);
          border-radius: 8px;

          @media (max-width: 575.98px) {
              max-width: 248px;
              margin: 0 auto;
          }

          .box__icon {
              display: flex;
              align-items: center;
              justify-content: center;
              margin-bottom: 14px;

              >.icon {
                  font-size: 34px;
                  color: #019DF4;
              }
          }

          .box__description {
              display: block;
              font-size: 12px;
              font-weight: 400;
              text-align: center;
              color: #50535A;
              margin: 0 auto;
              max-width: 180px;

              @media (min-width: 992px) {
                  font-size: 14px;
                  max-width: 210px;
              }

              >* {
                  font-size: 12px;
                  margin-bottom: 4px;

                  @media (min-width: 992px) {
                      font-size: 14px;
                  }

                  &:last-child {
                      margin-bottom: 0;
                  }
              }

              .text-tip {
                  color: #B6B7B7;
              }
          }
      }
  }
}